.bottom1{
    font-weight: 600;
    text-align: center;
    margin-bottom: 151px;
    position: relative;
    z-index: 1;

    & > .wrapper{
        flex-direction: column;
    }

    &__header{
        text-align: center;
        margin-bottom: 37px;
        h2{
            font-size: 30px;
            text-transform: uppercase;
        }
    
    }
    &__nav{
        position: relative;
        align-items: center;
        width: 464px;
        max-width: 100%;
        display: block;
        margin: 0 auto;
        margin-top: 31px;
        ul{
            display: flex;
            flex-wrap: wrap;
        }
    }

    &__menu{
        list-style-type: none;
        flex: 1 1 auto;

        a{
            padding: 8px 0;
            transition: all 0.1s ease;
        }

        &--active, &:hover{
            a{
                color: #33A0FF;
                border-bottom: 3px solid #33A0FF;
            }
        }
    }

    &__products{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        margin-bottom: 60px;

        &--margin{
            margin-bottom: 0;
        }
    }

    &__card{
        text-align: center;
        margin-bottom: 30px;
        cursor: pointer;
        position: relative;
        border: 3px solid #F6F7F8;
        padding: 30px 17px 0;
        width: 270px;
        max-width: 100%;

        &__add-to-cart{
            position: relative;
            display: inline-flex;
            border-radius: 50%;
            border: 2px solid RGBa(51, 160, 255, 0.3);
            width: 54px;
            height: 54px;
            z-index: 1;
            justify-content: center;
            align-items: center;

            svg{
                content: "\f07a";
                line-height: 1;
                font-size: 20px;
                color: #33A0FF;
            }

        }
        &--hot{
            &::before{
                content: "Hot";
                text-transform: uppercase;
                padding: 3px 8px;
                background: #FF4858;
                color: #fff;
                font-weight: 600;
                border-radius: 2px;
                position: absolute;
                top: 10px;
                left: 10px;
            }
        }

        &:hover {
            .bottom1__card__inner{
                transform: rotateY(180deg);
            }
        }

        &__flip{
            max-width: 100%;
            padding: 10px;
            width: 250px;
            margin-bottom: 48px;
            min-height: 230px;
        }

         &__back{
            position: absolute;
            width: 100%;
            height: 100%;
            -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
            top: 0;
            background: rgba(255, 255, 255, 0.9);
        }
        &__back{
            transform: rotateY(180deg);
        }

        &__favCart{
            position: absolute;
            top: 50%;
            left: 50%;
            width: 100%;
            transform: translate(-50%, -50%);
            i{
                margin: 0 5px;
            }
        }

        &__inner{
            position: relative;
            width: 100%;
            height: 100%;
            text-align: center;
            transition: transform 0.6s;
            transform-style: preserve-3d;
        }

        &__title{
            margin-bottom: 30px;
        }
        &__star{
            color: #FFC600;
            &:last-of-type{
                color: #C1C8CE;
            }
        }
        &__footer{
            font-size: 16px;
            margin-top: 12.35px;
            margin-bottom: 36px;
            font-weight: normal;
        }

        &__price{
            color: #FF4858;
            padding: 0 5px;
            &:last-of-type{
                color: #C1C8CE;
                text-decoration: line-through;
            }
        }
    }

    &__button{
        display: block;
        padding: 6px 0;
        color: #33A0FF;
        border-bottom: 3px solid #33A0FF;
        text-transform: uppercase;
        font-weight: bold;
        width: 85px;
        margin: 0 auto;
    }

}